'use client';
import { useEffect } from 'react';
import { client } from '../client';
import { redirect } from 'next/navigation';
import PocketBase from 'pocketbase';

const pb = new PocketBase(
	'http://pocketbase-lgc4cw80848w4w40swc4w8gw.samdez.net'
);
export default function MyPlaylists() {
	useEffect(() => {
		const authenticate = async () => {
			await client.authenticate();

			const currentUser = await client.currentUser.profile();
			const pbUser = await pb
				.collection('spotify_users')
				.getFirstListItem(`spotifyUserId = "${currentUser.id}"`);
			console.log('ðŸš€ ~ authenticate ~ pbUser:', pbUser);
			if (!pbUser) {
				await pb
					.collection('spotify_users')
					.create({ spotifyUserId: currentUser.id });
			}
			const playlists = await client.playlists
				.getUsersPlaylists(currentUser.id)
				.then(res =>
					res.items.filter(item => item.owner.id === currentUser.id)
				);
			console.log('ðŸš€ ~ authenticate ~ playlists:', playlists);
		};
		authenticate();
		// redirect('/my-playlists');
	}, []);
}
